<template>
  <div class="tk-console">

    <base-header class="pb-6">
      <div class="row align-items-center py-4">
        <div class="col-lg-6 col-7">
          <h6 class="h2 text-white d-inline-block mb-0">Administration</h6>
        </div>
        <div class="col-lg-6 col-5 text-right">
          <base-button
            size="sm"
            type="white"
            class="text-main"
            @click.stop="showAddAccount = true"
            >Create</base-button
          >
          <base-button
            size="sm"
            type="white"
            class="text-main"
            @click.stop="showAccountType = true"
            >Account Type</base-button
          >
        </div>
      </div>
    </base-header>

    <div class="container-fluid mt--6">
      <div class="card-wrapper">
        <div class="card">
          <div class="card-body">
            <el-table
              class="table-responsive align-items-center table-flush"
              header-row-class-name="thead-light"
              :data="account"
            >
              <el-table-column label="Name" prop="name"></el-table-column>
              <el-table-column label="Email" prop="email"></el-table-column>
              <el-table-column
                label="Password"
                prop="password"
              ></el-table-column>
              <el-table-column
                label="Sign up date"
                prop="signUpDate"
              ></el-table-column>
              <el-table-column
                label="Account type"
                prop="accountType"
              ></el-table-column>
              <el-table-column label="Logs">
                <template v-slot="{ row }">
                  <span>{{ row.logs }}</span>
                </template>
              </el-table-column>
              <el-table-column label="Permission">
                <template v-slot="{ row }">
                  <!-- <span>{{ row.permission }}</span> -->
                  <el-dropdown trigger="click" class="dropdown" style="width:100%;">
                    <div>{{ row.permission }}</div>
                    <el-dropdown-menu
                      class="dropdown-menu dropdown-menu-arrow show"
                      slot="dropdown"
                    >
                      <el-dropdown-item
                        v-for="item in accountType"
                        :key="item.typeName"
                        >
                        <div @click="changeAccountType(row, item)">{{ item.typeName }} ({{initPermissionCount(item)}})</div>
                      </el-dropdown-item>
                    </el-dropdown-menu>
                  </el-dropdown>
                </template>
              </el-table-column>
              <el-table-column width="54">
                <template v-slot="{ row }">
                  <div class="tk-console-table-fa-btn">
                    <i
                      class="fas fa-minus-circle tk-font-color-lightGray tk-px-margin-right-px20"
                      @click.stop="removeAccount(row)"
                    ></i>
                    <!-- <i
                      class="fas fa-plus-circle tk-font-color-main"
                      @click.stop="showAddAccount = true"
                    ></i> -->
                  </div>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
      </div>
    </div>

    <modal :show.sync="showAccountType" id="accountType">
      <template slot="header">
        <h5 class="modal-title">Account type</h5>
      </template>
      <div class="tk-popup-content">
        <div style="margin-bottom: 20px; text-align: center">
          <el-input
            v-model="newAccountType"
            placeholder="New account type"
            style="width: 300px"
          ></el-input>
          <span
            class="tk-font-color-main tk-px-margin-left-px20 tk-cursor-pointer"
            @click="addNewAccountType"
            >Add</span
          >
        </div>
        <el-table
          class="table-responsive align-items-center table-flush"
          header-row-class-name="thead-light"
          :data="accountType"
        >
          <el-table-column
            label="Account Type"
            prop="typeName"
          ></el-table-column>
          <el-table-column
            label="Shake to Report"
            prop="shakeToReport"
            width="88"
          >
            <template v-slot="{ row }">
              <span @click.stop="row.shakeToReport = !row.shakeToReport">
                <i
                  class="fas fa-check-square tk-font-color-main"
                  v-if="row.shakeToReport"
                ></i>
                <i class="far fa-square tk-font-color-lightGray" v-else></i>
              </span>
            </template>
          </el-table-column>
          <el-table-column label="Screen Shots" prop="screenShots" width="72">
            <template v-slot="{ row }">
              <span @click.stop="row.screenShots = !row.screenShots">
                <i
                  class="fas fa-check-square tk-font-color-main"
                  v-if="row.screenShots"
                ></i>
                <i class="far fa-square tk-font-color-lightGray" v-else></i>
              </span>
            </template>
          </el-table-column>
          <el-table-column label="Dynamic link" prop="dynamicLink" width="88">
            <template v-slot="{ row }">
              <span @click.stop="row.dynamicLink = !row.dynamicLink">
                <i
                  class="fas fa-check-square tk-font-color-main"
                  v-if="row.dynamicLink"
                ></i>
                <i class="far fa-square tk-font-color-lightGray" v-else></i>
              </span>
            </template>
          </el-table-column>
          <el-table-column label="Console" prop="console" width="88">
            <template v-slot="{ row }">
              <span @click.stop="row.console = !row.console">
                <i
                  class="fas fa-check-square tk-font-color-main"
                  v-if="row.console"
                ></i>
                <i class="far fa-square tk-font-color-lightGray" v-else></i>
              </span>
            </template>
          </el-table-column>
          <el-table-column label="Logs" prop="logs" width="60">
            <template v-slot="{ row }">
              <span @click.stop="row.logs = !row.logs">
                <i
                  class="fas fa-check-square tk-font-color-main"
                  v-if="row.logs"
                ></i>
                <i class="far fa-square tk-font-color-lightGray" v-else></i>
              </span>
            </template>
          </el-table-column>
          <el-table-column
            label="Administration"
            prop="administration"
            width="140"
          >
            <template v-slot="{ row }">
              <span @click.stop="row.administration = !row.administration">
                <i
                  class="fas fa-check-square tk-font-color-main"
                  v-if="row.administration"
                ></i>
                <i class="far fa-square tk-font-color-lightGray" v-else></i>
              </span>
            </template>
          </el-table-column>
          <el-table-column width="40">
            <template v-slot="{ row }">
              <div class="tk-console-table-fa-btn">
                <i
                  class="fas fa-minus-circle tk-font-color-lightGray tk-px-margin-right-px10"
                  @click="removeAccountType(row)"
                ></i>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <template slot="footer">
        <base-button
          type="secondary"
          @click.prevent="showAccountType = false"
          class="tk-font-color-lightGray tk-border-none"
          >Close</base-button
        >
        <base-button
          type="primary"
          @click.prevent="showAccountType = false"
          class="tk-font-color-white tk-bg-color-main tk-border-none"
          >Done</base-button
        >
      </template>
    </modal>

    <modal :show.sync="showAddAccount" id="addAccount">
      <template slot="header">
        <h5 class="modal-title">New Account</h5>
      </template>
      <div class="tk-popup-content">
        <div style="margin-bottom: 20px; text-align: center">
          <el-input
            v-model="name"
            placeholder="Name"
            style="width: 300px"
          ></el-input>
          <br />
          <br />
          <el-input
            v-model="email"
            placeholder="Email"
            style="width: 300px"
          ></el-input>
          <br />
          <br />
          <el-input
            v-model="password"
            placeholder="Password"
            style="width: 300px"
          ></el-input>
          <br />
          <br />
          <el-select v-model="currentAccountType" style="width: 300px">
            <el-option
              v-for="item in accountType"
              :key="item.typeName"
              :label="item.typeName"
              :value="item.typeName"
            >
            </el-option>
          </el-select>
        </div>
      </div>
      <template slot="footer">
        <base-button
          type="secondary"
          @click.prevent="showAddAccount = false"
          class="tk-font-color-lightGray tk-border-none"
          >Close</base-button
        >
        <base-button
          type="primary"
          @click.prevent="createNewAccount"
          class="tk-font-color-white tk-bg-color-main tk-border-none"
          >Create</base-button
        >
      </template>
    </modal>
  </div>
</template>

<script>
import $ from "jquery";
import {
  Table,
  TableColumn,
  DropdownMenu,
  DropdownItem,
  Dropdown,
  Tooltip,
  Select,
  Option,
} from "element-ui";
export default {
  components: {
    [Tooltip.name]: Tooltip,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
    [Dropdown.name]: Dropdown,
    [DropdownItem.name]: DropdownItem,
    [DropdownMenu.name]: DropdownMenu,
    [Select.name]: Select,
    [Option.name]: Option,
  },
  data() {
    return {
      // Name	eMail	Passowrd	Signup date	Account Type	Login times	Permission
      // Wayne Wang	mailwpw@gmail.com		1/2/2021	Administrator	5	5	-      +
      // Marine	marainlore@gmail.com		1/3/2021	Marketing	3	3	-      +
      // Zhang Yan Feng	zyflovelam@google.com		1/4/2021	R&D	3	3	-      +
      // Hai Tao	haitao@gmail.com		1/5/2021	R&D	3	3	-      +
      // Zhang Wei	zw1213321@gmail.com		1/6/2021	R&D	3	3	-      +
      // Test 12321	test@tunekey.com		1/7/2021	Testing	2	2	-      +
      // Tunekey stu test	teafda@fdsa.com		1/8/2021	Testing	2	2	-      +
      showAccountType: false,
      newAccountType: "",
      showAddAccount: false,
      name: "",
      email: "",
      password: "",
      currentAccountType: "",
      newAccount: {
        id: "",
        name: "",
        email: "",
        password: "",
        signUpDate: this.$tools.formatMMDDYY(new Date().getTime()),
        accountType: "",
        logs: 0,
        permission: 0,
      },

      account: [
        {
          id: 0,
          name: "Wayne Wang",
          email: "mailwpw@gmail.com",
          password: "Spelist100",
          signUpDate: "1/2/2021",
          accountType: "Administrator",
          logs: 5,
          permission: 6,
        },
        {
          id: 1,
          name: "Marine",
          email: "marainlore@gmail.com",
          password: "Spelist100",
          signUpDate: "1/3/2021",
          accountType: "Marketing",
          logs: 5,
          permission: 3,
        },
        {
          id: 2,
          name: "Zhang Yan Feng",
          email: "zyflovelam@gmail.com",
          password: "Spelist100",
          signUpDate: "1/4/2021",
          accountType: "R&D",
          logs: 3,
          permission: 5,
        },
        {
          id: 3,
          name: "Test121",
          email: "test@tunekey.com",
          password: "123456",
          signUpDate: "1/5/2021",
          accountType: "Testing",
          logs: 3,
          permission: 1,
        },
      ],

      accountType: [
        {
          id: 0,
          typeName: "Administrator",
          shakeToReport: true,
          screenShots: true,
          dynamicLink: true,
          console: true,
          logs: true,
          administration: true,
        },
        {
          id: 1,
          typeName: "R&D",
          shakeToReport: true,
          screenShots: true,
          dynamicLink: true,
          console: true,
          logs: true,
          administration: false,
        },
        {
          id: 2,
          typeName: "Testing",
          shakeToReport: true,
          screenShots: false,
          dynamicLink: false,
          console: false,
          logs: false,
          administration: false,
        },
        {
          id: 3,
          typeName: "Marketing",
          shakeToReport: false,
          screenShots: true,
          dynamicLink: true,
          console: false,
          logs: false,
          administration: true,
        },
      ],
    };
  },
  mounted() {
    this.initClick();
  },
  methods: {
    changeAccountType(row, item) {
      console.log(row,item)
      let index = 0;
      this.account.some((item, i) => {
        if (item.id == row.id) {
          index = i;
          return;
        }
      });
      // row.permission = this.initPermissionCount(item);
      this.account[index].permission = this.initPermissionCount(item);
      this.account[index].accountType = item.typeName;
    },
    initPermissionCount(item) {
      let permission = 0;
      if (item.shakeToReport) {
        permission++;
      }
      if (item.dynamicLink) {
        permission++;
      }
      if (item.screenShots) {
        permission++;
      }
      if (item.console) {
        permission++;
      }
      if (item.logs) {
        permission++;
      }
      if (item.administration) {
        permission++;
      }
      return permission;
    },
    initClick() {
      $(".el-table__row").unbind("mouseover");
      $(".el-table__row").unbind("mouseleave");
      for (let i = 0; i < $(".el-table__row").length; i++) {
        let index = i;
        $(".el-table__row")
          .eq(index)
          .mouseover(() => {
            $(".el-table__row")
              .eq(index)
              .find(".tk-console-table-fa-btn")
              .show();
          });
        $(".el-table__row")
          .eq(index)
          .mouseleave(() => {
            $(".el-table__row")
              .eq(index)
              .find(".tk-console-table-fa-btn")
              .hide();
          });
      }
    },
    async createNewAccount() {
      let self = this;
      if (self.$tools.isNull(self.name)) {
        self.$notify({
          message: "Name is empty!",
          type: "error",
        });
        return false;
      }

      if (self.$tools.isNull(self.email)) {
        self.$notify({
          message: "Email is empty!",
          type: "error",
        });
        return false;
      }

      if (self.$tools.isNull(self.password)) {
        self.$notify({
          message: "Password is empty!",
          type: "error",
        });
        return false;
      }

      if (self.$tools.isNull(self.currentAccountType)) {
        self.$notify({
          message: "Account type is empty!",
          type: "error",
        });
        return false;
      }

      let exist = false;
      await self.account.some((item, i) => {
        if (item.email == self.email) {
          exist = true;
          return;
        }
      });

      let accountTypeIndex = 0
      await self.accountType.some((item, i) => {
        if (item.typeName == self.currentAccountType) {
          accountTypeIndex = i
          return
        }
      })

      if (!exist) {
        self.account.push({
          id: new Date().getTime(),
          name: self.name,
          email: self.email,
          password: self.password,
          signUpDate: self.$tools.formatMMDDYY(new Date().getTime()),
          accountType: self.currentAccountType,
          logs: 0,
          permission: self.initPermissionCount(self.accountType[accountTypeIndex]),
        });
        setTimeout(() => {
          self.name = "";
          self.email = "";
          self.password = "";
          self.currentAccountType = "";
          self.showAddAccount = false;
          self.initClick();
        }, 10);
      } else {
        self.$notify({
          message: "Duplicate email!",
          type: "warning",
        });
      }
    },
    removeAccount(row) {
      let index = 0;
      this.account.some((item, i) => {
        if (item.id == row.id) {
          index = i;
          return;
        }
      });
      this.account.splice(index, 1);
    },
    removeAccountType(row) {
      let index = 0;
      this.accountType.some((item, i) => {
        if (item.id == row.id) {
          index = i;
          return;
        }
      });
      this.accountType.splice(index, 1);
    },
    async addNewAccountType() {
      let isExist = false;

      await this.accountType.some((item, i) => {
        if (item.typeName == this.newAccountType) {
          isExist = true;
          return;
        }
      });

      if (isExist) {
        this.$notify({
          message: "Duplicate account type!",
          type: "warning",
        });
      } else {
        if (this.newAccountType == "") {
          this.$notify({
            message: "Empty value!",
            type: "error",
          });
        } else {
          this.accountType.push({
            id: this.newAccountType,
            typeName: this.newAccountType,
            shakeToReport: false,
            screenShots: false,
            dynamicLink: false,
            console: false,
            logs: false,
            administration: false,
          });
          self.initClick();
        }
      }

      this.newAccountType = "";
    },
  },
};
</script>

<style>
.tk-console .cell i {
  font-size: 1.2rem !important;
}

#accountType .modal-dialog {
  max-width: 800px !important;
}

#accountType .modal-content {
  width: 800px !important;
}

#accountType th {
  padding: 0 10px !important;
}
#accountType td {
  padding: 10px !important;
}
#accountType .modal-body {
  padding-top: 0 !important;
}
#accountType .tk-popup-content {
  max-height: 64vh;
}
.tk-console-table-fa-btn {
  display: none;
}
#addAccount .el-input__inner:focus {
  border-color: #71d9c2 !important;
}
</style>